<template>
  <div>
    <div class="containerBox">

      <!-- 暂无内容页面内容 -->
      <!-- <el-empty description="开发中，暂无内容"></el-empty> -->
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">处方列表</span>
        <router-link to="/prescription/save">
          <el-button type="primary" icon="el-icon-plus">添加处方</el-button>
        </router-link>
      </div>
      <!-- 列表 -->
      <div>
        <el-table :data="psData" style="width: 100%">
          <el-table-column prop="psName" label="处方名称"></el-table-column>
          <el-table-column prop="psAnode" label="阳极区"></el-table-column>
          <el-table-column prop="psCathode" label="阴极区"></el-table-column>
          <el-table-column prop="psCurrent" label="电流(μA)"></el-table-column>
          <el-table-column prop="psDuration" label="时长(min)"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <div>
                <el-link size="mini" type="primary" @click="gotoEdit(scope.row.psId)">编辑</el-link>&nbsp;&nbsp;&nbsp;
                <el-link size="mini" type="primary" @click="deletePs(scope.row.psId)">删除</el-link>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="paging">
        <el-pagination @current-change="changePage"
                       :current-page="paginationData.page"
                       :hide-on-single-page="paginationData.size/paginationData.pageSize <= 1"
                       :page-size="paginationData.pageSize"
                       layout="total, prev, pager, next"
                       :total="paginationData.size">
        </el-pagination>
      </div>

    </div>
  </div>
</template>
<script>
import AppServerInf from "@/common/serverinf"

export default {
  data() {
    return {
      psData: [],
      paginationData: {
        page: 1,
        pageSize: 10,
        size: 0
      },
    }
  },
  created() {
    this.initPsList(1)
  },
  methods: {
    initPsList(page) {
      this.psData = this.$db.psList()
    },
    changePage(val) {
      this.initLeaderList(val)
    },
    gotoEdit(psId) {
      this.$router.push(`/prescription/${psId}/save`)
    },
    deletePs(psId) {
      this.$confirm('是否删除该处方?', '提示',{}).then(()=>{
        this.$db.deletePs(psId)
        this.psData = this.$db.psList()
      })
    }
  }
}
</script>
<style>
</style>



